<template>
  <transition name="slide">
    <div class="wrapper">
      <div class="header">
        <i class="back" @click="back"></i>
        <span class="text">我的购物车</span>
      </div>
      <div class="content-wrapper">
        <scroll class="content">
          <div class="list">
            <!-- 在组件上面遍历的时候，需要加上:key，否则事件中的item取不到 https://vuejs.org/v2/guide/list.html#key -->
            <left-swipe v-for="(item,index) in shoppingCartItems" @delete="deleteItem(item.id)" :key="index">
              <good-item
              ref="goodItem"
              class="item"
              @count="calc"
              :hasCheckBox="hasCheckItem"
              :item="item"
              >
              </good-item>
            </left-swipe>
          </div>
        </scroll>
        <div class="settle-wrapper">
          <settle
            confirmText="提交订单"
            ref="settle"
            @submit="submit"
            :hasCheckBox="hasCheckAll"
            :price="price"
            :total="total"
          >
          </settle>
        </div>
      </div>
      <alert ref="alert" :message="alertMsg"></alert>
      <router-view></router-view>
    </div>
  </transition>
</template>


<script type="text/ecmascript-6">
  import LeftSwipe from 'base/left-swipe/left-swipe'
  import GoodItem from 'base/good-item/good-item'
  import Alert from 'base/alert/alert'
  import Scroll from 'base/scroll/scroll'
  import Settle from 'base/settle/settle'
  import {mapActions,mapGetters} from 'vuex'

  export default {
    data () {
      return {
        hasCheckItem: false,
        hasCheckAll: false,
        alertMsg: '',
        total: 0,
        price: 0
      }
    },
    computed: {
      ...mapGetters([
        'shoppingCartItems'
      ])
    },
    created () {
      console.log(this.shoppingCartItems)
    },
    mounted () {
      let goodItems = this.$refs.goodItem
      if(goodItems && goodItems.length){
        for(let i = 0; i < goodItems.length; i++){
          let goodItem = goodItems[i]
          goodItem.setCount(goodItem.item.count)
        }
      }
    },
    methods: {
      back() {
        this.$router.back()
      },
      deleteItem(id){
        let shoppingCartItems = {}
        for(let key in this.shoppingCartItems){
          if(id !== this.shoppingCartItems[key].id){
            shoppingCartItems[key] = this.shoppingCartItems[key]
          }
        }
        this.setShoppingCart(shoppingCartItems)
        this.calcPriceAndTotal()
      },
      calc(item){
        this.setShoppingCart({
          ...this.shoppingCartItems,
          [item.id]:item
        })
        this.calcPriceAndTotal()
      },
      // 计算数量和价格
      calcPriceAndTotal() {
        let tempTotal = 0
        let tempPrice = 0
        for(let key in this.shoppingCartItems){
          let tempItem = this.shoppingCartItems[key]
          tempTotal += tempItem.count
          tempPrice += tempItem.price * tempItem.count
        }
        this.total = tempTotal
        this.price = tempPrice
      },
      showAlert() {
        this.$refs.alert.show()
      },
      // 跳转到提交订单模块
      submit(){
        if(this.total){
          this.$router.push({
            path: `${this.$router.currentRoute.path}/confirm`
          })
        } else {
          this.alertMsg = '购物车为空'
          this.showAlert()
        }
      },
      ...mapActions([
        'setShoppingCart'
      ])
    },
    components: {
      LeftSwipe,
      Alert,
      Settle,
      GoodItem,
      Scroll
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slide-enter,.slide-leave-to
    transform translate3d(100%,0,0)

  .slide-enter-active,.slide-leave-active
    transition all 0.3s

  .wrapper
    position absolute
    top 0
    left 0
    right 0
    bottom 0
    z-index 1
    background $color-background
    .header
      position fixed
      height 44px
      top 0
      left 0
      right 0
      background $color-header-background
      display flex
      justify-content center
      align-items center
      .back
        position absolute
        background url("~common/image/back.png") no-repeat
        background-size contain
        width 8px
        height 15px
        left 15px
        top 15px
        &:before
          position absolute
          left -10px
          right -10px
          top -10px
          bottom -10px
          content ''
      .text
        color $color-header-text
        font-size $font-size-large
        font-weight bold
    .content-wrapper
      position absolute
      left 0
      right 0
      top 44px
      bottom 0
      background $color-background
      display flex
      justify-content center
      .content
        width 100%
        overflow hidden
        margin-top 10px
        .list
          .item
            height 110px
      .settle-wrapper
        position fixed
        left 0
        right 0
        bottom 0
        height 45px

</style>




