<template>
  <div class="good">
    <div class="left" v-if="hasCheckBox" @click="toggleItem">
      <i ref="checkbox" :class="checked === true ? 'active' : ''"></i>
    </div>
    <div class="middle">
      <img class="img" :src="item.src" />
    </div>
    <div class="right">
      <div class="detail">
        <p class="name">{{item.name}}</p>
        <div class="desc">
          <p>
            <label>规格：</label>
            <span>{{item.desc}}</span>
          </p>
          <p class="price">
            <label>￥</label>
            <span>{{item.price|formatMoney}}</span>
            <label>/{{item.unit}}</label>
          </p>
        </div>
      </div>
      <div class="operate">
        <i class="minus" @click="minus" v-show="showIcon"></i>
        <div class="input-wrapper" v-show="showIcon">
          <input type="text" v-model="count" />
        </div>
        <i class="plus" @click="plus"></i>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import {hasClass,addClass,removeClass} from 'common/js/dom'

  export default {
    data () {
      return {
        count: 0,
        checked: false,
        showIcon: false
      }
    },
    filters: {
      formatMoney(value){
        if(!isFinite(value)){
          return ''
        }
        return value.toFixed(2)
      }
    },
    props: {
      hasCheckBox: {
        type: Boolean,
        default: false
      },
      item: {
        type: Object,
        default: () => {
          return {}
        }
      }
    },
    methods: {
      minus() {
        this.count = Math.max(0, this.count - 1)
      },
      plus(){
        this.count = parseInt(this.count) + 1
      },
      setCount(count) {
        this.count = count
      },
      setChecked(value) {
        this.checked = value
      },
      toggleItem(){
        let checkbox = this.$refs.checkbox
        if(hasClass(checkbox, 'active')){
          this.checked = false
        } else　{
          this.checked = true
        }
      }
    },
    watch: {
      // 如果监听对象，需要设置deep:true，并且只能监听初始化时候的属性
      // 官方文档讲的很清楚，监听是注册的时候对属性批量使用Object.defineProperty方法实现的，所以后添加的属性不会监听
      count(newValue) {
        if(newValue === 0){
          this.showIcon = false
        } else {
          this.showIcon = true
        }
        this.$emit('count', {
            ...this.item,
            count:newValue === '' ? 0 : parseInt(newValue)
        })
      },
      checked(newValue) {
        this.$emit('checked', newValue, this.item)
      }
    },
    components: {

    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
    @import "~common/stylus/variable"

    .good
      width 100%
      height 100%
      display flex
      background #fff
      .left
        display flex
        justify-content flex-end
        align-items center
        width 24px
        i
          display flex
          background url("./unchecked.png") no-repeat
          background-size contain
          width 13px
          height 13px
          position relative
          &.active
            background url("./checked.png") no-repeat
            background-size contain
          &:before
            position absolute
            left -10px
            top -10px
            right -10px
            bottom -10px
            content ''
      .middle
        width 80px
        height 100%
        display flex
        align-items center
        justify-content center
        .img
          height 70px
          width 70px
          display block
      .right
        display flex
        flex 1
        border-bottom 1px solid #F3F3F3
        .detail
          display flex
          flex 1
          flex-direction column
          .name
            font-size $font-size-medium-x
            color #4c4c4c
            margin-top 20px
            font-weight bold
          .desc
            display flex
            flex-direction column
            color #4c4c4c
            font-size $font-size-small-x
            margin-top 12px
          .price
            display flex
            color #eb4747
            margin-top 12px
            align-items flex-end
            label
              font-size $font-size-small-x
            span
              font-weight bold
              font-size $font-size-medium-x
              margin-left 4px
              margin-right 2px
        .operate
          margin-bottom 15px
          display flex
          justify-content flex-end
          align-items flex-end
          flex-basis 115px
          margin-right 15px
          .minus
            display flex
            background url("./minus.png") no-repeat
            background-size contain
            width 25px
            height 25px
          .input-wrapper
            display flex
            height 23px
            flex-basis 35px
            margin-left 10px
            margin-right 10px
            input
              align-items center
              width 100%
              height 100%
              box-sizing border-box
              border 1px solid #FFC848
              border-radius 6px
              text-align center
          .plus
            display flex
            background url("./plus.png") no-repeat
            background-size contain
            width 25px
            height 25px

</style>



