<template>
    <div class="delete">
        <div class="slider" ref="slider" :style="deleteSlider">
            <div class="swipe-wrapper"
                @touchstart="touchStart"
                @touchmove="touchMove"
                @touchend="touchEnd"
            >
                <slot></slot>
            </div>
            <div class="delete" ref="delete" @click="deleteItem">
                {{deleteText}}
            </div>
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
    export default {
      props: {
        deleteText: {
          type: String,
          default: '删除'
        },
        deleteWidth: {
          type: String,
          default: '80'
        },
        height: {
          type: String,
          default: '110'
        }
      },
      data() {
        return {
          startX: 0,   //触摸位置
          endX: 0,     //结束位置
          moveX: 0,    //滑动时的位置
          disX: 0,     //移动距离
          deleteSlider: '',//滑动时的效果,使用:style="deleteSlider"
        }
      },
      mounted () {
        this.$refs.slider.style.height = `${this.height}px`
        this.$refs.delete.style.width = `${this.deleteWidth}px`
        this.$refs.delete.style.right = `-${this.deleteWidth}px`
      },
      methods:{
        deleteItem() {
          this.$emit('delete')
        },
        touchStart(ev){
          ev= ev || event
          //tounches类数组，等于1时表示此时有只有一只手指在触摸屏幕
          if(ev.touches.length == 1){
            // 记录开始位置
            this.startX = ev.touches[0].clientX
          }
        },
        touchMove(ev){
          ev = ev || event
          //获取删除按钮的宽度，此宽度为滑块左滑的最大距离
          let btn = this.$refs.delete
          let offsetWidth = this.$refs.delete.offsetWidth
          if(ev.changedTouches.length == 1) {
            // 滑动时距离浏览器左侧实时距离
            this.moveX = ev.changedTouches[0].clientX
            //实时距离减去起始位置，得到手指实时偏移距离
            this.disX = this.moveX - this.startX
            //具体滑动距离我取的是 手指偏移距离
            this.deleteSlider = `transform:translateX(${this.disX}px)`
            // 最大也只能等于删除按钮宽度
            // 如果小于0表示是左移
            if(this.disX < 0) {
              if (-this.disX >= offsetWidth / 2) {
                this.deleteSlider = `transform:translateX(-${offsetWidth}px)`
              }
            } else if(this.disX > 0) {
              if (this.disX >= offsetWidth / 2) {
                this.deleteSlider = `transform:translateX(0)`
              }
            }
          }
        },
        touchEnd(ev){
          ev = ev || event
          let offsetWidth = this.$refs.delete.offsetWidth
          if (ev.changedTouches.length == 1) {
            this.endX = ev.changedTouches[0].clientX
            this.disX = this.endX - this.startX
            if(this.disX < 0) {// 如果是向左滑
              if (-this.disX > offsetWidth / 2) {//大于一半 滑动到最大值
                this.deleteSlider = `transform:translateX(-${offsetWidth}px)`
              } else {//如果距离小于删除按钮一半,强行回到起点
                this.deleteSlider = `transform:translateX(0)`
              }
            } else if(this.disX > 0) {// 如果是向右滑
              //大于一半 滑动到最大值
              if (this.disX > offsetWidth / 2) {
                // debugger
                this.deleteSlider = `transform:translateX(0)`
              } else {
                this.deleteSlider = `transform:translateX(-${offsetWidth}px)`
              }
            }
          }
        }
      }
    }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
    @import "~common/stylus/variable"

    .slider
      width 100%
      height 100%
      user-select none
      position relative
      transition all 0.3s
      .swipe-wrapper
        width 100%
        height 100%
      .delete
        position absolute
        top 0
        bottom 0
        display flex
        justify-content center
        align-items center
        height 100%
        background #eb4747
        color #fff
        text-align center
        font-size $font-size-medium-m

</style>



